/* ==========================================================================
    Utility Navigation
========================================================================== */

.c-utility {
    @include padding();
    position: relative;
    display: block;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: 1em;
    width: 100%;
    padding-top: $gutter-xs;
    padding-bottom: $gutter-xs;
    background-color: $gray--darkest;
    color: $white;
}

/**
 * === MENU ===
 */

.c-utility__menu {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    column-gap: 1em;
    font-size: $font-base-sm;
}

