/* ==========================================================================
    Archives
========================================================================== */

.t-default.is-archive {

    /**
     * === PRODUCT GRID ===
     */
    .c-grid.is-products {
        margin-top: $gutter;
        margin-bottom: $gutter;
    }

    /**
     * === SEARCH RESULTS ===
     */
    .o-page.is-search-results {
        .c-banner.is-default.no-image {
            margin-bottom: $gutter;
            background-color: $white--dark;
        }
        .o-section.is-list {
            @include padding();
            margin-bottom: $gutter;
        }
        .c-pagination {
            margin-bottom: $gutter;
        }
    }

}
